<!-- eslint-disable -->
<template>
  <div id="equipa" class="equipa-wrapper">
    <div class="equipa">
      <div class="left lateral">
        <div class="person-wrapper">
          <div class="team">
            <div class="person" :class="{'active': isActive}" v-for="p in people" :key="p.id">
              <div class="pic" :style="{ 'background-image': 'url(' + p.pic + ')' }"></div>
              <div class="name">{{ p.name }}</div>
              <div class="description">
                <i v-html="p.text"></i>
              </div>
            </div>
          </div>
          <div class="indicators">
            <div
              class="idc"
              v-for="p in people"
              :key="p.id"
              :class="{'active': isActive}"
              @click="currentSlide(p.id)"
            ></div>
          </div>
        </div>
      </div>
      <div class="right lateralR">
        <div class="subtext">estamos aqui para si</div>
        <div class="title main">A nossa equipa</div>
        <div class="line"></div>
        <div class="txt">
          <p
            class="p"
            :class="{'active': isActive}"
            v-for="p in people"
            :key="p.id"
            v-html="p.content"
          ></p>
        </div>
        <div class="quote">
          <!-- <div class="icon" :style="{ 'background-image': 'url(' + quote + ')' }"></div> -->
          <div class="txt" :class="{'active': isActive}" v-for="p in people" :key="p.id">
            <i class="qt" v-html="p.quote"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import quote from "../../assets/quote.png";
import pic from "../../assets/carolina.jpg";

export default {
  data() {
    return {
      quote: quote,
      isActive: true,
      index: 1,
      people: [
        {
          id: 1,
          name: "Ana Carolina Pereira",
          text: "Direcção Clínica",
          content:
            '2008. Foi neste ano que concluiu o Mestrado em Psicologia Clínica pela Universidade de Coimbra Faculdade de Psicologia e de Ciências da Educação. Dos corredores da Faculdade, que continua a visitar, "passou" para a prática clínica. <br>2009. Teve o privilégio, que continua a ter, de trabalhar com Mestres que muito a ensinaram (e continuam a ensinar) a aliar o conhecimento à clínica. À Clínica Bebés e Crescidos, Clínica de Psicologia e Saúde Familiar, nomeadamente ao Professor Doutor Eduardo Sá e à Dra. Raquel Vieira da Silva, o meu obrigada por continuar a ser uma verdadeira "universidade" de aprendizagem clínica. E de amizade. <br>2018. Concluiu doutoramento em Psicologia Clínica pela Universidade de Coimbra, Faculdade de Psicologia e de Ciências da Educação. <br>Em todo o seu percurso, a sua área de atuação versou sobre a psicologia clínica. A par, foi integrando a participação e a organização de seminários/congressos/conferências. Pontualmente, desenvolve seminários no âmbito da avaliação psicológica (técnicas projetivas e avaliação do desenvolvimento) e prática clínica. Aliar o conhecimento clínico ao ensino e prática formativa será, porventura, uma das "janelas de conhecimento" que muito privilegia, pelo que o valor da Academia é, para si, inestimável. Pelo que ensina e promove (assim deveria) a pensar. Ensinar e aprender com criatividade será, porventura, para si, o móbil que tentará desenvolver e que guiará (assim pretende) os seus objetivos. Os seus sonhos.',
          quote:
            "Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.",
          pic: pic
        },
        {
          id: 2,
          name: "Liliana Sousa",
          text: "Another quote",
          content: "Content to be shown here",
          quote: "Quote to be shown here",
          pic: ""
        }
      ]
    };
  },
  methods: {
    clickIndicator(n) {
      var i;
      var slides = document.querySelectorAll(".team .person");
      var dots = document.querySelectorAll(".indicators .idc");
      var text = document.querySelectorAll(".right .txt .p");
      var quote = document.querySelectorAll(".quote .txt");
      if (n > slides.length) {
        this.index = 1;
      }
      if (n < 1) {
        this.index = slides.length;
      }
      for (i = 0; i < slides.length; i++) {
        slides[i].className = "person";
        text[i].className = "p";
        quote[i].className = "txt";
      }
      for (i = 0; i < dots.length; i++) {
        dots[i].className = dots[i].className.replace(" active", "");
        this.isActive = false;
      }
      slides[this.index - 1].className = "person active";
      dots[this.index - 1].className += " active";
      text[this.index - 1].className += " active";
      quote[this.index - 1].className += " active";
      this.isActive = true;
    },
    plusSlides(n) {
      this.clickIndicator((this.index += n));
    },
    currentSlide(n) {
      this.clickIndicator((this.index = n));
    }
  },
  mounted() {
    this.clickIndicator(this.index);
  }
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquoise: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.equipa-wrapper {
  background-color: #fafafa;
  height: pxToEm(655);
  padding: $pd-desktop;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
    height: auto;
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  .equipa {
    margin: auto;
    max-width: pxToEm(1400);
    width: 100%;
    display: flex;
    justify-content: space-between;

    @media only screen and (max-width: 991px) {
      flex-direction: column;
      align-items: center;
    }

    .left {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      @media only screen and (max-width: 991px) {
        margin-bottom: pxToEm(40);
        width: 100%;
      }

      .person-wrapper {
        width: pxToEm(240);
        height: pxToEm(345);
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: pxToEm(60);
        position: relative;
        overflow: hidden;

        @media only screen and (max-width: 991px) {
          width: 100%;
          padding: 3.75em 0;
          margin-top: pxToEm(50);
          height: 100%;
        }

        .team {
          display: flex;
          justify-content: space-between;
          position: relative;
          width: 100%;
          height: 100%;

          .person {
            display: flex;
            flex-direction: column;
            align-items: center;
            position: absolute;
            width: 100%;
            opacity: 0;
            transition: 0.5s;

            &.active {
              opacity: 1;
            }

            .pic {
              border-radius: $circle;
              width: pxToEm(90);
              height: pxToEm(90);
              background-color: #cbcbcb;
              background-size: cover;
            }

            .name {
              font-size: pxToEm(16);
              color: $turquoise;
              margin-top: pxToEm(20);
            }

            .description {
              font-family: Georgia, "Times New Roman", Times, serif;
              font-size: pxToEm(20);
              color: #828282;
              text-align: center;
              margin-top: pxToEm(30);
            }
          }
        }

        .indicators {
          display: flex;
          justify-content: space-around;
          align-items: center;
          width: 25%;
          margin-top: pxToEm(320);

          @media screen and (max-width: 991px) {
            width: 5%;
          }

          .idc {
            width: pxToEm(10);
            height: pxToEm(10);
            background-color: #bce0fd;
            border-radius: $circle;
            cursor: pointer;

            &.active {
              background-color: $turquoise;
            }
          }
        }
      }
    }

    .right {
      max-width: pxToEm(900);
      width: 100%;
      height: pxToEm(440);
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;

      @media only screen and (max-width: 991px) {
        align-items: center;
        text-align: center;
        height: 50%;
        width: 100%;
        max-width: 100%;
      }

      .subtext {
        font-size: pxToEm(16);
        text-transform: uppercase;
        color: #828282;
      }

      .title {
        margin-top: pxToEm(20);
        color: #828282;
      }

      .line {
        width: pxToEm(80);
        height: pxToEm(1);
        background-color: $turquoise;
        margin-top: pxToEm(10);
      }

      > .txt {
        font-family: "OS-Regular";
        font-size: pxToEm(14);
        color: #828282;
        margin: pxToEm(20) 0;
        min-height: pxToEm(220);

        @media only screen and (max-width: 991px) {
          width: 100%;
          max-width: 100%;
          // min-height: pxToEm(160);
          margin-top: pxToEm(10);
          height: auto;
        }

        @media only screen and (max-width: 580px) {
          height: pxToEm(300);
        }

        @media only screen and (max-width: 440px) {
          height: pxToEm(350);
        }

        @media only screen and (max-width: 370px) {
          height: pxToEm(400);
        }

        @media only screen and (max-width: 330px) {
          height: pxToEm(450);
        }

        .p {
          position: absolute;
          opacity: 0;
          transition: 0.5s;

          @media only screen and (max-width: 991px) {
            height: auto;
            margin: 0;
          }

          &.active {
            opacity: 1;
          }
        }
      }

      .quote {
        margin-top: pxToEm(20);

        @media only screen and (max-width: 991px) {
          width: 100%;
          max-width: 100%;
          min-height: pxToEm(100);
          margin-bottom: pxToEm(20);
        }

        .icon {
          width: pxToEm(16);
          height: pxToEm(15);
          background-size: cover;
        }

        > .txt {
          font-family: Georgia, "Times New Roman", Times, serif;
          font-size: pxToEm(20);
          color: #828282;
          margin-top: pxToEm(15);
          opacity: 0;
          transition: 0.5s;
          position: absolute;

          &.active {
            opacity: 1;
          }
        }
      }
    }
  }
}
</style>



